.productBox{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-content: space-around;
  gap: 30px;
}
.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  max-width: 300px;
  margin: auto;
  text-align: center;
  font-family: arial;
}

.card img{
  width: 100%;
}

.price {
  color: grey;
  font-size: 22px;
}

.card button {
  border: none;
  outline: 0;
  padding: 12px;
  color: white;
  background-color: #000;
  text-align: center;
  cursor: pointer;
  width: 100%;
  font-size: 18px;
}

.cartButton{
  position: absolute;
  right: 0;
  padding: 10px 20px;
  font-size: 25px;
  border-radius: 10px;
  text-decoration: none;
  background-color: aqua;
}

.card button:hover {
  opacity: 0.7;
}

.cartBox{
  margin-top: 100px;
}

.cartItem{
  display: grid;
  grid-template-columns: repeat( 6 , 1fr) ;
}

.cartItem img{
  justify-self: center;
  align-self: center;
  grid-column: 1/2;
  width: 60%;
}
.cartItem .proInfo{
  justify-self: center;
  align-self: center;
  grid-column: 2/4;
}
.cartItem .itemQuantity{
  justify-self: center;
  align-self: center;
  grid-column: 4/5;
}
.price{
  justify-self: center;
  align-self: center;
  grid-column: 5/5;
}

.removebtn{
  justify-self: center;
  align-self: center;
  grid-column: 6/6;
}

